    class="about-container_bottom-wrapper__item about-container_bottom-wrapper__item--2"
    @click="openPhoto2"
  >
    <img
      :src="`${Photo2_img}`"
      alt="about-container_bottom-wrapper image 2"
      class="about-container_bottom-wrapper__img"
      data-aos="zoom-in"
      data-aos-duration="500"
    />

    <div
      class="about-container_bottom-wrapper__item about-container_bottom-wrapper__item--2-overlay"
    >
      <h1 class="about-container_bottom-wrapper__item--2-overlay_title">
        {{ Photo2_overlayText }}
        <br />
        {{ Photo2_overlayText2 }}
      </h1>
    </div>
  </figure>
</template>

<script>
export default {
  props: {
    Photo2_img: {
      type: String,
      required: true,
    },

    Photo2_overlayText: {
      type: String,
      required: true,
    },

    Photo2_overlayText2: {
      type: String,
      required: true,
    },
  },

  methods: {
    openPhoto2() {
      this.$emit("openPhoto2");
    },
  },
};
</script>

<style lang="scss">
@import "SASS/base/_Home.scss";
