<template>
  <transition name="slide-fade">
    <div class="about-modal__background">
      <div class="about-modal__content">
        <div class="about-modal__close" @click="closeModal">
          &times;
        </div>
        <div class="about-modal__content--logo">
          <img v-bind:src="`${modalImg}`" class="about-modal__content--logo-img" alt="Dlive Full_Logo_Original" />
        </div>

        <h1 class="about-modal__content--title">{{ modalTitle }}</h1>

        <p class="about-modal__content--paragraph1">
          {{ modalText }}
        </p>

        <button
          class="about-modal__content--nextBtn"
          @click="nextPhoto"
          v-if="modalCarousel"
        >
          &rarr;
        </button>
        <button
          class="about-modal__content--prevBtn"
          @click="prevPhoto"
          v-if="modalCarousel"
        >
          &larr;
        </button>

        <!-- <p class="about-modal__content--paragraph2">
            </p> -->
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: "Modal",


  props: {
    modalCarousel: {
      type: Boolean,
      required: false,
    },

    modalImg: {
      type: String,
      required: true,
    },

    modalTitle: {
      type: String,
      required: true,
    },

    modalText: {
      type: String,
      required: true,
    },
  },

  methods: {
    closeModal() {
      this.$emit("closeModal");
    },
    nextPhoto() {
      this.$emit("nextPhoto");
    },
    prevPhoto() {
      this.$emit("prevPhoto");
    },
  },
};
</script>

<style lang="scss">
@import "SASS/base/_Projects.scss";
</style>
