<template>
  <div>
    <nav class="navbar-container">
      <div
        class="navbar-container_icon"
        @click="toggleNav()"
        :class="{ active: isOpen }"
      >
        <span class="navbar-container_line1"></span>
        <span class="navbar-container_line2"></span>
        <span class="navbar-container_line3"></span>
      </div>
    </nav>

    <transition name="slide-fade">
      <nav class="navcontent" v-if="slideIn">
        <ul
          class="navcontent-ul"
          :class="{ navOpen: isOpen, displayNone: displayClosed }"
        >
          <router-link
            @click.native="removeNav()"
            to="/"
            class="navcontent-ul--home"
            id="dropdown-home"
          >
            <h1 class="navcontent-ul--home-title">
              Home
            </h1>
          </router-link>

          <router-link
            @click.native="removeNav()"
            to="/projects"
            class="navcontent-ul--projects"
            id="dropdown-projects"
          >
            <h1 class="navcontent-ul--projects-title">
              Projects
            </h1>
          </router-link>

          <router-link
            @click.native="removeNav()"
            to="/school-projects"
            class="navcontent-ul--school-projects"
            id="dropdown-school-projects"
          >
            <h1 class="navcontent-ul--school-projects-title">
              School-Projects
            </h1>
          </router-link>

          <router-link
            @click.native="removeNav()"
            to="/contact"
            class="navcontent-ul--contact"
            id="dropdown-contact"
          >
            <h1 class="navcontent-ul--contact-title">
              Contact
            </h1>
          </router-link>
        </ul>
      </nav>
    </transition>
  </div>
</template>

<script>
export default {
  name: "NavbarComp",
  props: {},
  data() {
    return {
      title: "Hai På Graaten".toUpperCase(),
      isOpen: false,
      displayClosed: true,
      slideIn: false,
      transitionNav: "all .5s ease-out",
    };
  },
  methods: {
    toggleNav() {
      // document.documentElement.classList.toggle("overflow");
      this.isOpen = !this.isOpen;
      this.displayClosed = !this.displayClosed;
      this.slideIn = !this.slideIn;
    },

    removeNav() {
      this.isOpen = !this.isOpen;
      this.displayClosed = !this.displayClosed;
      this.slideIn = !this.slideIn;
    },
  },
};
</script>

<style lang="scss">
@import "SASS/base/_navbar.scss";
</style>
