<template>
  <transition name="slide-fade">
    <div class="photo-modal__background">
      <div class="photo-modal__content">
        <div class="photo-modal__close" @click="closeModal">
          &times;
        </div>
        <img
          v-bind:src="`${modalImg}`"
          class="photo-modal__content--img"
          alt="Dlive Full_Logo_Original"
        />
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: "Modal",

  props: {
    modalImg: {
      type: String,
      required: true,
    },
  },

  methods: {
    closeModal() {
      this.$emit("closeModal");
    },
  },
};
</script>

