<template>
  <figure
    class="about-container_bottom-wrapper__item about-container_bottom-wrapper__item--1"
    @click="openPhoto1"
  >
    <img
      :src="`${Photo1_img}`"
      alt="about-container_bottom-wrapper image 1"
      class="about-container_bottom-wrapper__img"
      data-aos="zoom-in"
      data-aos-duration="500"
    />

    <div
      class="about-container_bottom-wrapper__item about-container_bottom-wrapper__item--1-overlay"
    >
      <h1 class="about-container_bottom-wrapper__item--1-overlay_title">
        {{ Photo1_overlayText }}
      </h1>
    </div>
  </figure>
</template>

<script>
export default {
  props: {
    Photo1_img: {
      type: String,
      required: true,
    },

    Photo1_overlayText: {
      type: String,
      required: true,
    },
  },

  methods: {
    openPhoto1() {
      this.$emit("openPhoto1");
    },
  },
};
</script>

<style lang="scss">
@import "SASS/base/_Home.scss";
