import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Projects from "../views/Projects.vue";
import SchoolProjects from "../views/SchoolProjects.vue";
import Contact from "../views/Contact.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/projects",
    name: "Projects",
    component: Projects,
  },
  {
    path: "/school-projects",
    name: "SchoolProjects",
    component: SchoolProjects,
  },
  {
    path: "/contact",
    name: "Contact",
    component: Contact,
  },
];

const router = new VueRouter({
  mode: "history",
  routes,
  linkExactActiveClass: "active", // active class for *exact* links.
  scrollBehavior() {
    window.scrollTo(0, 0);
  },
});

export default router;
